import React, { Component } from 'react';

import '../CSS/Certificates.css';
import Trybe01 from '../imagesPortifolio/Trybe01.jpg';
import LucasPB from '../imagesPortifolio/LucasPB.jpg'

export default class Certificates extends Component {
  constructor(props) {
    super(props);

    this.handleCertificate = this.handleCertificate.bind(this);
  }

  handleCertificate() {
    const location = window.location.pathname;
    const certificateId = location.slice(13);
    switch (certificateId) {
      case 'trybe01':
        return (<img
          src={ Trybe01 }
          alt="certificado Trybe - Fundamentos do Desenvolvimento Web"
        />);
      default:
        return (
          <div>
            <span>Selecione um certificado!</span>
            <img src={ LucasPB } alt="Lucas em PB" />
          </div>
        );
    }
  }

  render() {
    return (
      <section className="certificates-container">
        { this.handleCertificate() }
      </section>
    );
  }
}
